             <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;其结果是，皮姆复兴八个问题后作为被打扮的超级英雄 蚂蚁人谁在13页，三章的故事主演的“蚂蚁蚂蚁人/陆军的回报/蚂蚁人的复仇”的故事令人惊讶＃35（1962年9月）。角色的冒险成为标题中的持续特征。问题＃44（1963年6月）以他的社交名媛女友和实验室助理珍妮特·范戴恩的首次亮相为特色。珍妮特采用了黄蜂的服装身份，并在Pym随后出演Tales to Astonish时共同主演。黄蜂还偶尔担任标题中备份故事的框架序列主持人。1963年9月，李和柯比创造了超级英雄头衔The Avengers并且Ant-Man和Wasp在问题＃1中成立，作为团队的创始成员。</blockquote>
